import React, { useState } from 'react'
import Image from 'next/image'

import { MailIcon } from "@heroicons/react/solid";
import Button from '../Button/Button';

function NavLink({ to, children, ...props }) {
    return <a href={to} {...props}>
        {children}
    </a>
}

const Footer = ({ ...props }) => {
    const footerLinks = [
        {
            title: 'Resources', content: [
                { name: 'User Guide', to: '/' },
                { name: 'Blog', to: '/' },
                { name: 'Give Feedback', to: '/' },
            ]
        },
        {
            title: 'Legal', content: [
                { name: 'Term of Use', to: '/' },
                { name: 'Privacy Policy', to: '/' },
            ]
        },
        {
            title: 'Stats', content: [
                { name: 'Ranking', to: '/' },
                { name: 'Activities', to: '/' },
            ]
        },
        {
            title: 'Service', content: [
                { name: 'Buy NFT', to: '/' },
            ]
        },
        {
            title: 'Company', content: [
                { name: 'Contact Us', to: '/' },
                { name: 'About Us', to: '/' },
            ]
        },
    ];
    return (
        <div className='flex flex-col px-9'>
            <div className='flex pt-14'>
                <div className='flex flex-col'>
                    <div className="flex items-center mb-6">
                        <a className="flex items-center text-2xl font-semibold" href="/">
                            <Image src="/assets/logo.svg" alt='logo' width={59} height={43} />
                            <div className='pl-3'>Poulina</div>
                        </a>
                    </div>
                    <span className='text-base leading-6 text-rolling-stone max-w-[230px] mb-4'>Please reach out to us on the email below if you have any idea, complain or request</span>
                    <a className='flex items-center' href='mailto:nft@populina.com'>
                        <MailIcon
                            className='w-5 h-5 mr-3 text-black'
                            aria-hidden="true"
                        />
                        nft@poulina.com
                    </a>
                </div>
                <div className='flex flex-row flex-1 justify-evenly'>
                    {
                        footerLinks.map((item, idx) => (
                            <div className='flex flex-col'>
                                <span className='title text-black/[0.8] font-medium text-xl mb-6'>{item.title}</span>
                                {
                                    item.content.map((cItem, cIdx) => (
                                        <NavLink to={cItem.to} className='text-lg text-black/[0.5] mb-4'>{cItem.name}</NavLink>
                                    ))
                                }
                            </div>
                        ))
                    }
                </div>
            </div>
            <div className='flex items-center pt-20 pb-8'>
                <Button type="button" icon="youtube" className='h-8 mr-10 w-11'></Button>
                <Button type="button" icon="instagram" className='w-8 h-8 mr-10'></Button>
                <Button type="button" icon="linkedin" className='w-8 h-8 mr-10'></Button>
                <Button type="button" icon="facebook" className='w-8 h-8 mr-24'></Button>
                <Button type="button" icon="copyright" iconClass="mr-2" className='flex items-center text-black/[0.5]'>2022 NFT Poulina inc.</Button>
            </div>
        </div >
    )
}


export default Footer;


