import { useState } from "react";
import Image from "next/image";
import Search from "../Search/Search";
import Button from "../Button/Button";
import Link from 'next/link'

function NavLink({ to, children }) {
  return (
    <a href={to} className={`text-lg	mx-4`}>
      {children}
    </a>
  );
}

function MobileHeader({ open, setOpen }) {
  return (
    <div
      className={`absolute top-0 left-0 h-screen w-screen bg-white transform ${
        open ? "-translate-x-0" : "-translate-x-full"
      } transition-transform duration-300 ease-in-out filter drop-shadow-md `}
    >
      <div className="flex items-center justify-center h-20 bg-white filter drop-shadow-md">
        {" "}
        {/*logo container*/}
        <a className="text-xl font-semibold" href="/index-back">
          <Image src="/assets/logo.svg" alt="logo" width={100} height={45} />
        </a>
      </div>
      <div className="flex flex-col ml-4">
        <a
          className="my-4 text-xl font-medium"
          href="/"
          onClick={() =>
            setTimeout(() => {
              setOpen(!open);
            }, 100)
          }
        >
          Explore
        </a>
        <a
          className="my-4 text-xl font-normal"
          href="/"
          onClick={() =>
            setTimeout(() => {
              setOpen(!open);
            }, 100)
          }
        >
          Stats
        </a>
        <a
          className="my-4 text-xl font-normal"
          href="/"
          onClick={() =>
            setTimeout(() => {
              setOpen(!open);
            }, 100)
          }
        >
          Join Forum
        </a>
        <a
          className="my-4 text-xl font-normal"
          href="/create-item"
          onClick={() =>
            setTimeout(() => {
              setOpen(!open);
            }, 100)
          }
        >
          Create NFT
        </a>
      </div>
    </div>
  );
}

export default function Header() {
  const [open, setOpen] = useState(false);
  return (
    <nav className="flex items-center px-8 bg-white shadow-lg py-9 h-28 filter drop-shadow-md">
      <MobileHeader open={open} setOpen={setOpen} />

      <div className="flex items-center w-1/2">
        <div className="items-center hidden md:flex">
          <a
            className="flex items-center text-2xl font-semibold"
            href="/index-back"
          >
            <Image src="/assets/logo.svg" alt="logo" width={59} height={43} />
            <div className="pl-3">Poulina</div>
          </a>
        </div>
        <div className="flex items-center justify-around flex-1">
          <div
            className="relative z-50 flex flex-col items-center justify-between w-8 h-8 md:hidden"
            onClick={() => {
              setOpen(!open);
            }}
          >
            {/* hamburger button */}
            <span
              className={`h-1 w-full bg-black rounded-lg transform transition duration-300 ease-in-out ${
                open ? "rotate-45 translate-y-3.5" : ""
              }`}
            />
            <span
              className={`h-1 w-full bg-black rounded-lg transition-all duration-300 ease-in-out ${
                open ? "w-0" : "w-full"
              }`}
            />
            <span
              className={`h-1 w-full bg-black rounded-lg transform transition duration-300 ease-in-out ${
                open ? "-rotate-45 -translate-y-3.5" : ""
              }`}
            />
          </div>

          <div className="hidden md:flex">
            <NavLink to="/">Explore</NavLink>
            <NavLink to="/">Stats</NavLink>
            <NavLink to="/">Join Forum</NavLink>
            <NavLink to="/create-item">Create NFT</NavLink>
          </div>
        </div>
      </div>
      <div className="items-center justify-between hidden w-1/2 px-6 md:flex">
        <Search />
        <Button
          type="button"
          className="rounded-md py-2.5 px-9 text-lg border border-solid border-orange text-orange"
        >
          <Link href="/pay-for-item">
            <a className="mr-6 text-pink-500">
              Wallet connect
            </a>
          </Link>
        </Button>
      </div>
    </nav>
  );
}



